import React from 'react';
import PropTypes from 'prop-types';
import Icon from './Icon.jsx';
import csx from 'classnames';
import css from './Settings.css';

export default class Settings extends React.PureComponent {
  static propTypes = {
    settings: PropTypes.object,
    onChange: PropTypes.func
  };

  constructor (props) {
    super(props);
    this.state = {};
  }

  onChange = (key, value) => {
    const { settings } = this.props;
    const newSettings = { ...settings };
    newSettings[key] = !!value;
    this.props.onChange(newSettings);
  };

  renderSetting ({ label, id, help }) {
    const { settings } = this.props;
    const isChecked = !!settings[id];
    return (
      <p className={css.row}>
        <label id={'label_' + id} htmlFor={id}>{label}</label>
        <span
          className={csx(css.checkbox, isChecked && css.checked)}
          aria-labelledby={'label_' + id}
          role="checkbox"
          aria-checked={isChecked}
          onClick={() => this.onChange(id, !isChecked)}
          tabIndex="0"
          >
          <Icon name={isChecked ? 'toggle_on' : 'toggle_off'} size={40} />
        </span>
        {help ? <span className={css.help}>{help}</span> : null}
      </p>
    );
  }

  render () {
    return (
      <form className={css.settings}>
        <h2>Stillingar</h2>
        {this.renderSetting({
          label: 'Erfiðari viðureign',
          id: 'hardMode',
          help: 'Þekktir stafir verða að vera notaðir í eftirfarandi ágiskunum.'
        })}
        {this.renderSetting({
          label: 'Gefa 6 stafi',
          id: 'handicap6',
          help: 'Leikurinn byrjar með 6 ónotaða stafi sýnilega.'
        })}
        {this.renderSetting({
          label: 'Hefðbundið lyklaborð',
          id: 'qwerty',
          help: 'Nota „ERTY...“ uppröðun á lyklaborði frekar en „AÁBD...“'
        })}
      </form>
    );
  }
}
